(function() {

    'use strict';

    angular
        .module('project')
        .service('googleMapPositionService', googleMapPositionService);

    googleMapPositionService.$inject = ['$q', '$window'];

    function googleMapPositionService($q, $window) {
        var apiReady = $q.defer();

        activate();

        this.onReady = onReady;

        function onReady() {
            return apiReady.promise;
        }

        function activate() {
            var tag = document.createElement('script');
            tag.src = "https://maps.googleapis.com/maps/api/js?callback=initMap";
            var firstScriptTag = document.getElementsByTagName('script')[0];
            firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);

            var secTag = document.createElement('script');
            secTag.src = "http://www.geoplugin.net/javascript.gp";
            var secondScriptTag = document.getElementsByTagName('script')[0];
            secondScriptTag.parentNode.insertBefore(secTag, secondScriptTag);

            $window.initMap = apiReady.resolve;
        }
    }
})();

(function(){
    'use strict';

    angular
        .module('project')
        .factory('GeolocationService', GeolocationService);

    GeolocationService.$inject = ['$q', '$window', 'googleMapPositionService', 'errorHandling'];

    function GeolocationService($q, $window, googleMapPositionService, errorHandling) {
        var currentLocation = {
            getCurrentPosition: getCurrentPosition,
            actualPosition: actualPosition,
            markerPosition: markerPosition,
            positionFlag: false
        };

        return currentLocation;

        function markerPosition(lat, lng) {
            var position = {};
            position.latitude = lat;
            position.longitude = lng;
            currentLocation['position'] = position;
            currentLocation.positionFlag = true;
        }

        function getCurrentPosition() {
            var deferred = $q.defer();
            googleMapPositionService.onReady()
            .then(function(){
                var lng = geoplugin_longitude(),
                    lat = geoplugin_latitude(),
                    geoPosition = {
                        coords: {
                            latitude: lat,
                            longitude: lng
                        }
                    };
                if (!$window.navigator.geolocation) {
                    deferred.reject('Geolocation not supported.');
                } else {
                    $window.navigator.geolocation.getCurrentPosition(
                        function (position) {
                            deferred.resolve(position);
                        },
                        function () {
                            deferred.resolve(geoPosition);
                        });
                }
            });
            return deferred.promise;
        }

        function actualPosition() {
            var position = {};
            var deferred = $q.defer();
            if(!currentLocation.positionFlag){
                getCurrentPosition()
                    .then(function(geoposition){
                    position.latitude = geoposition.coords.latitude;
                    position.longitude = geoposition.coords.longitude;
                    currentLocation['position'] = position;
                    deferred.resolve(position);
                    })
                    .catch(function(err){
                        errorHandling.errorFunc(err);
                    });
            }else{
                deferred.resolve(currentLocation.position);
            }
            return deferred.promise;
        }

    }

})();

(function() {
    'use strict';

    angular
        .module('project')
        .factory('firebaseService', firebaseService);

    firebaseService.$inject = ['$firebaseArray', '$q', 'firebaseUrl', 'userService'];

    function firebaseService($firebaseArray, $q, firebaseUrl, userService) {
        var fireBase = {
            addComment: addComment,
            getPlaceComments: getPlaceComments,
            addUser: addUser,
            addToFavorite: addToFavorite,
            getFavoritePlaces: getFavoritePlaces,
            removeFavoritePlace: removeFavoritePlace,
            addToTrip: addToTrip,
            getExcursionPlaces: getExcursionPlaces,
            removePlaceFromTrip: removePlaceFromTrip,
            updateTripElement: updateTripElement
        };
        return fireBase;

        function addComment(placeId, obj) {
            var defer = $q.defer();
            var placeRef = new Firebase(firebaseUrl+'comments/'+placeId);

            placeRef.push(obj);
            defer.resolve();

            return defer.promise;
        }

        function getPlaceComments(placeId) {
            var defer = $q.defer();
            var commentsArr = [];
            var placeCommentsRef = new Firebase(firebaseUrl+'comments/'+placeId);

            commentsArr = $firebaseArray(placeCommentsRef);
            defer.resolve(commentsArr);

            return defer.promise;
        }

        function addUser(userObj) {
            var defer = $q.defer();
            var userRef = new Firebase(firebaseUrl+'users/'+userObj.id);

            userRef.update(userObj);
            defer.resolve();

            return defer.promise;
        }

        function addToTrip(item) {
            var defer = $q.defer();
            var userId = userService.user.id;
            var excursionRef = new Firebase(firebaseUrl+'users/'+userId+'/excursion');

            excursionRef.push(item);
            defer.resolve();

            return defer.promise;
        }

        function updateTripElement(place) {
            var defer = $q.defer();
            var userId = userService.user.id;
            var excursionRef = new Firebase(firebaseUrl+'users/'+userId+'/excursion/'+place.fireId);

            excursionRef.update(place);
            defer.resolve();

            return defer.promise;
        }

        function getExcursionPlaces(userObj){
            var defer = $q.defer();
            var excursionArr = [];
            var excursionRef = new Firebase(firebaseUrl+'users/'+userObj.id+'/excursion');

            excursionArr = $firebaseArray(excursionRef);
            defer.resolve(excursionArr);

            return defer.promise;
        }

        function removePlaceFromTrip(userObj, place) {
            var excursionRef = new Firebase(firebaseUrl+'users/'+userObj.id+'/excursion/'+place.$id);
            excursionRef.remove();
        }

        function addToFavorite(item) {
            var defer = $q.defer();
            var userId = userService.user.id;
            var facovritePlacesRef = new Firebase(firebaseUrl+'users/'+userId+'/favorites');

            facovritePlacesRef.push(item);
            defer.resolve();

            return defer.promise;
        }

        function getFavoritePlaces(userObj){
            var defer = $q.defer();
            var favoriteArr = [];
            var facovritePlacesRef = new Firebase(firebaseUrl+'users/'+userObj.id+'/favorites');

            favoriteArr = $firebaseArray(facovritePlacesRef);
            defer.resolve(favoriteArr);

            return defer.promise;
        }

        function removeFavoritePlace(userObj, place) {
            var facovritePlacesRef = new Firebase(firebaseUrl+'users/'+userObj.id+'/favorites/'+place.$id);
            facovritePlacesRef.remove();
        }
    }

})();

(function(){
    'use strict';

    angular
        .module('project')
        .factory('FacebookService', FacebookService);

    FacebookService.$inject = ['$rootScope', '$window', '$location', 'errorHandling', 'firebaseService', '$timeout', 'GeolocationService', '$q', 'userService', 'cacheService'];

    function FacebookService($rootScope, $window, $location, errorHandling, firebaseService, $timeout, GeolocationService, $q, userService, cacheService) {
        var service = {
            checkLoginState: checkLoginState,
            login: login,
            logout: logout,
            setRange: setRange,
            rangeDistance: 500,
            getMuseums: getMuseums,
            getBars: getBars,
            getCafes: getCafes,
            getClubs: getClubs,
            getHotels: getHotels,
            getRestaurants: getRestaurants,
            getDetails: getDetails,
            getComments: getComments,
            getMoreData: getMoreData,
            getUploadedPhotos: getUploadedPhotos,
            loginResponse: ''
        };

        return service;

        function getUploadedPhotos(id) {
            var deferred = $q.defer();
            FB.api(
                '/'+id+'/photos',
                'GET',
                {"type":"uploaded","fields":"id,name,source"},
                function(response) {
                    if (!response || response.error) {
                        deferred.reject('An error occurred while retrieving data');
                    } else {
                        deferred.resolve(response);
                    }
                }
            );
            return deferred.promise;
        }

        function setRange(range) {
            service.rangeDistance = range;
        }

        function getMoreData(request) {
            var deferred = $q.defer();
            FB.api(request,
                function(response) {
                    if (!response || response.error) {
                        deferred.reject('An error occurred while retrieving data');
                    } else {
                        deferred.resolve(response);
                    }
                }
            );
            return deferred.promise;
        }

        function getDetails(id) {
            var deferred = $q.defer();
            FB.api(
                '/'+id,
                'GET',
                {"fields":["name","price_range","engagement",
                "restaurant_specialties","restaurant_services",
                "website","payment_options","phone","about",
                "category", "description", "food_styles",
                "general_info","location","link","picture.type(large)"]},
                function(response) {
                    if (!response || response.error) {
                        deferred.reject('An error occurred while retrieving data');
                    } else {
                        deferred.resolve(response);
                    }
                }
            );
            return deferred.promise;
        }

        function getComments(id) {
            var deferred = $q.defer();
            FB.api(
                '/'+id+'/posts',
                'GET',
                {"limit":"3"},
                function(response) {
                    if (!response || response.error) {
                        deferred.reject('An error occurred while retrieving data');
                    } else {
                        deferred.resolve(response);
                    }
                }
            );
            return deferred.promise;
        }

        function getMuseums() {
            var deferred = $q.defer();
            GeolocationService.actualPosition()
            .then(function(position){
                FB.api(
                    '/search',
                    'GET',
                    {"q":"muzeum",
                    "type":"place",
                    "center": position.latitude+","+position.longitude ,
                    "distance": service.rangeDistance,
                    "fields":["hours","description","name", "location", "picture.type(large)"]},
                    function(response) {
                        if (!response || response.error) {
                            deferred.reject('An error occurred while retrieving data');
                        } else {
                            deferred.resolve(response);
                        }
                    }
                );
            });
            return deferred.promise;
        }

        function getBars() {
            var deferred = $q.defer();
            GeolocationService.actualPosition()
            .then(function(position){
                FB.api(
                    '/search',
                    'GET',
                    {"q":"bar",
                    "type":"place",
                    "center": position.latitude+","+position.longitude ,
                    "distance": service.rangeDistance,
                    "fields":["hours","description","name", "location", "picture.type(large)"]},
                    function(response) {
                        if (!response || response.error) {
                            deferred.reject('An error occurred while retrieving data');
                        } else {
                            deferred.resolve(response);
                        }
                    }
                );
            });
            return deferred.promise;
        }

        function getCafes() {
            var deferred = $q.defer();
            GeolocationService.actualPosition()
            .then(function(position){
                FB.api(
                    '/search',
                    'GET',
                    {"q":"cafe",
                    "type":"place",
                    "center": position.latitude+","+position.longitude ,
                    "distance": service.rangeDistance,
                    "fields":["hours","description","name", "location", "picture.type(large)"]},
                    function(response) {
                        if (!response || response.error) {
                            deferred.reject('An error occurred while retrieving data');
                        } else {
                            deferred.resolve(response);
                        }
                    }
                );
            });
            return deferred.promise;
        }

        function getClubs(){    /// danceing party
            var queries = ['club', 'disco', 'nightclub', 'dance', 'discotheque', 'party'];
            var fullArray = [];
            var deferred = $q.defer();
            GeolocationService.actualPosition()
            .then(function(position){
                angular.forEach(queries, function(query){
                    FB.api(
                        '/search',
                        'GET', {
                        "q": query,
                        "type":"place",
                        "center": position.latitude+","+position.longitude ,
                        "distance": service.rangeDistance,
                        "fields":["hours","description","name", "location", "picture.type(large)"]
                        },
                        function(response) {
                            if (!response || response.error) {
                                deferred.reject('An error occurred while retrieving data');
                            } else {
                                fullArray.push(response);
                                deferred.resolve(response);
                            }
                        }
                    );
                });
            });
            return deferred.promise;
        }

        function getHotels(){
            var deferred = $q.defer();
            GeolocationService.actualPosition()
            .then(function(position){
                FB.api(
                    '/search',
                    'GET',
                    {"q":"hotel",
                    "type":"place",
                    "center": position.latitude+","+position.longitude ,
                    "distance": service.rangeDistance,
                    "fields":["hours","description","name", "location", "picture.type(large)"]},
                    function(response) {
                        if (!response || response.error) {
                            deferred.reject('An error occurred while retrieving data');
                        } else {
                            deferred.resolve(response);
                        }
                    }
                );
            });
            return deferred.promise;
        }

        function getRestaurants(){
            var deferred = $q.defer();
            GeolocationService.actualPosition()
            .then(function(position){
                FB.api(
                    '/search',
                    'GET',
                    {"q":"restaurant",
                    "type":"place",
                    "center": position.latitude+","+position.longitude ,
                    "distance": service.rangeDistance,
                    "fields":["hours","description","name", "location", "picture.type(large)"]},
                    function(response) {
                        if (!response || response.error) {
                            deferred.reject('An error occurred while retrieving data');
                        } else {
                            deferred.resolve(response);
                        }
                    }
                );
            });
            return deferred.promise;
        }

        function getUserData() {
            var deferred = $q.defer();
            FB.api(
                '/me',
                'GET',
                {"fields":["id","name","picture","first_name","last_name","hometown","location","link"]},
                function(response) {
                    if (!response || response.error) {
                        deferred.reject('An error occurred while retrieving data');
                        logout(response.error.message);
                        errorHandling.error('Something goes wrong...');
                    } else {
                        deferred.resolve(response);
                    }
                }
            );
            deferred.promise.then(function(response){
                var user = saveUserData(response);
                service.loginResponse = 'Thanks for logging in, ' + response.name + '!';
                return user;
            }).then(function(userObj){
                firebaseService.addUser(userObj);
                loadUserPlaces(userObj);
            });
            return deferred.promise;
        }

        function loadUserPlaces(userObj) {
            firebaseService.getFavoritePlaces(userObj)
                .then(function(result){
                    cacheService.saveFavorite(result);
                });
            firebaseService.getExcursionPlaces(userObj)
                .then(function(result){
                    cacheService.saveTrip(result);
                });
        }

        function saveUserData(data){
            userService.user.id = data.id;
            userService.user.first_name = data.first_name;
            userService.user.last_name = data.last_name;
            userService.user.full_name = data.name;
            userService.user.profile_link = data.link;
            userService.user.picture = data.picture.data.url;
            userService.user.happy = true;

            return userService.user;
        }

        function checkLoginState() {
            var deferred = $q.defer();
            FB.getLoginStatus(function(response) {
                statusChangeCallback(response)
                .then(function(){
                    deferred.resolve();
                })
                .catch(function(){
                    errorHandling.warnFunc('Please login to continue.');
                    deferred.reject();
                });
            });
            return deferred.promise;
        }

        function statusChangeCallback(response) {
            var deferred = $q.defer();
            if (response.status === 'connected') {
                if(!userService.user.happy){
                    getUserData().then(function(){
                        deferred.resolve();
                    });
                }else{ deferred.resolve(); }

                userService.user.connected = true;
            } else {
                $window.location.href = '#/';
                deferred.reject();
            }
            return deferred.promise;
        }

        function login(){
            FB.login(function(response){
                if (response.status === 'connected') {
                    statusChangeCallback(response);
                }
            });
        }

        function logout(feed) {
            var feedback = feed || 'You have been logged out.';
            FB.logout(function() {
                $timeout(function(){
                    userService.user.connected = false;
                    $window.location.href = '#/';
                    errorHandling.infoFunc(feedback);
                },0);
            });
        }

    }

})();

(function(){
    'use strict';

    angular
        .module('project')
        .factory('errorHandling', errorHandling);

    errorHandling.$inject = ['toastr'];

    function errorHandling(toastr) {
        var catcher = {
            errorFunc: errorFunc,
            infoFunc: infoFunc,
            warnFunc: warnFunc,
            successFunc: successFunc
        };
        return catcher;

        function errorFunc(msg) {
            toastr.error(msg, "Error occurred!");
        }

        function infoFunc(msg) {
            toastr.info(msg, "Information");
        }

        function warnFunc(msg) {
            toastr.warning(msg, "Warning");
        }

        function successFunc(msg) {
            toastr.success(msg, "Success");
        }
    }


})();


(function() {
    'use strict';

    angular
        .module('project')
        .service('cacheService', cacheService);

    cacheService.$inject = ['categories'];

    function cacheService(categories) {

        var self = this;
        self.list = {};
        self.favorite = [];
        self.excursion = [];

        self.getCache = getCache;
        self.saveCache = saveCache;
        self.clearCache = clearCache;
        self.saveFavorite = saveFavorite;
        self.getFavorite = getFavorite;
        self.getTrip = getTrip;
        self.saveTrip = saveTrip;

        clearCache();

        function getFavorite() {
            return self.favorite;
        }

        function saveFavorite(favoriteArr) {
            self.favorite = favoriteArr;
        }

        function getTrip() {
            return self.excursion;
        }

        function saveTrip(tripArr) {
            self.excursion = tripArr;
        }

        function saveCache(category, list){
            self.list[category] = list;
        }

        function getCache() {
            return self.list;
        }

        function clearCache() {
            categories.forEach(function(item){
                self.list[item] = [];
            });
        }

    }

})();

(function() {
    'use strict';

    angular
        .module('project')
        .factory('httpInterceptor', function ($q, $rootScope) {

            var numLoadings = 0;
            return {
                request: function (config) {
                    numLoadings++;
                    $rootScope.$broadcast("loader_show");
                    return config || $q.when(config)
                },
                response: function (response) {
                    if ((--numLoadings) === 0) {
                        $rootScope.$broadcast("loader_hide");
                    }
                    return response || $q.when(response);
                },
                responseError: function (response) {
                    if (!(--numLoadings)) {
                        $rootScope.$broadcast("loader_hide");
                    }
                    return $q.reject(response);
                }
            };
        })
        .config(function ($httpProvider) {
            $httpProvider.interceptors.push('httpInterceptor');
        });
})();

(function() {
    'use strict';

    angular
        .module('project')
        .directive("spinner", spinner);

        function spinner() {
            return {
                restrict: 'A',
                link: function($scope, element) {
                    $scope.$on("loader_show", function () {
                        return element.removeClass('hide');
                    });
                    return $scope.$on("loader_hide", function () {
                        return element.addClass('hide');
                    });
                }
            }
        }
})();

