(function() {
    'use strict';

    angular
        .module('project')
        .directive('ceGoogleMapRoutes', ceGoogleMapRoutes);

    ceGoogleMapRoutes.$inject = ['GeolocationService', '$localStorage', 'cacheService'];

    function ceGoogleMapRoutes(GeolocationService, $localStorage, cacheService) {
        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/googleMapRoutes/googleMapRoutes.html',
            scope: {
                favchanged: '='
            },
            link: linkFunc
        };
        return directive;

        function linkFunc(scope){

            scope.$watch('favchanged', initMap, true);
            scope.distance = {};

            function initMap() {
                GeolocationService.actualPosition()
                .then(function(actualPos){
                    var pointA = new google.maps.LatLng(actualPos.latitude, actualPos.longitude),

                        myOptions = {
                            zoom: 18,
                            center: pointA
                        },

                        map = new google.maps.Map(document.getElementById("map_favorite"), myOptions),
                        directionsService = new google.maps.DirectionsService,
                        directionsDisplay = new google.maps.DirectionsRenderer({
                            map: map
                        });

                    return [directionsService, directionsDisplay, pointA];
                }).then(function(result){
                    calculateAndDisplayRoute(result[0],result[1],result[2]);

                    document.getElementById('mode').addEventListener('change', function() {
                        calculateAndDisplayRoute(result[0],result[1],result[2]);
                    });
                });

                function calculateAndDisplayRoute(directionsService, directionsDisplay, pointA) {
                    var selectedMode = document.getElementById('mode').value;
                    var approvedToShow = [],
                        endLocation, PointB,
                        waypoints = [];

                    cacheService.excursion.forEach(function(element){
                        if(element.showOnMap) {
                            approvedToShow.push(element);
                        }
                    });

                    endLocation = approvedToShow[approvedToShow.length-1];

                    if (endLocation) {
                        PointB = new google.maps.LatLng(endLocation.location.latitude, endLocation.location.longitude)
                    } else {
                        PointB = ''
                    }

                    approvedToShow.forEach(function(element) {
                        waypoints.push({
                            location: new google.maps.LatLng(element.location.latitude, element.location.longitude)
                        });
                    });

                    waypoints.pop();

                    directionsService.route({
                        origin: pointA,
                        destination: PointB,
                        waypoints: waypoints,
                        avoidTolls: true,
                        avoidHighways: false,
                        travelMode: google.maps.TravelMode[selectedMode]
                    }, function (response, status) {
                        if (status == google.maps.DirectionsStatus.OK) {
                            calculateDistance(response.routes[0].legs);
                            directionsDisplay.setDirections(response);
                        }
                    });
                }
            }

            function calculateDistance(response) {
                var tempDist = null, tempDur = null;
                response.forEach(function(element) {
                    tempDist += element.distance.value;
                    tempDur += element.duration.value;
                });
                scope.distance.value = Math.round((tempDist/1000) * 100) / 100 + " km";
                scope.distance.duration = Math.round((tempDur/60) * 100) / 100 + "min";
            }
        }
    }

})();


(function() {
    'use strict';

    angular
        .module('project')
        .directive('ceGoogleMapPosition', ceGoogleMapPosition);

    ceGoogleMapPosition.$inject = ['GeolocationService', 'cacheService'];

    function ceGoogleMapPosition(GeolocationService, cacheService) {
        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/googleMapPosition/googleMapPosition.html',

            link: function(scope){

                GeolocationService.actualPosition()
                .then(function(position){
                    scope.actualCity = geoplugin_city();
                    scope.actualCountry = geoplugin_countryName();

                    var map = new google.maps.Map(document.getElementById("map_canvas"), {
                        zoom: 12,
                        center: new google.maps.LatLng(position.latitude, position.longitude),
                        mapTypeId: google.maps.MapTypeId.ROADMAP
                    });

                    var myMarker = new google.maps.Marker({
                        position: new google.maps.LatLng(position.latitude, position.longitude),
                        draggable: true
                    });

                    google.maps.event.addListener(myMarker, 'dragend', function (evt) {
                        GeolocationService.markerPosition(evt.latLng.lat().toFixed(3), evt.latLng.lng().toFixed(3));
                        cacheService.clearCache();
                    });

                    map.setCenter(myMarker.position);
                    myMarker.setMap(map);
                });
            }
        };
        return directive;
    }

})();

(function() {
    'use strict';

    angular
        .module('project')
        .directive('ceGoogleMap', ceGoogleMap);

    ceGoogleMap.$inject = ['GeolocationService'];

    function ceGoogleMap(GeolocationService) {
        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/googleMap/googlemap.html',
            scope: {
                position: '='
            },
            link: linkFunc
        };
        return directive;

        function linkFunc(scope){

            scope.$watch('position', initMap, true);

            scope.distance = {};

            function initMap(position) {
                GeolocationService.actualPosition()
                .then(function(actualPos){
                    var pointA = new google.maps.LatLng(actualPos.latitude, actualPos.longitude),
                        pointB = new google.maps.LatLng(position.lat,position.lng),

                        myOptions = {
                            zoom: 18,
                            center: pointA
                        },

                        map = new google.maps.Map(document.getElementById("map"), myOptions),
                        directionsService = new google.maps.DirectionsService,
                        directionsDisplay = new google.maps.DirectionsRenderer({
                            map: map
                        });

                    // get route from A to B
                    return [directionsService, directionsDisplay, pointA, pointB];
                }).then(function(result){
                    calculateAndDisplayRoute(result[0],result[1],result[2],result[3]);

                    document.getElementById('mode').addEventListener('change', function() {
                        calculateAndDisplayRoute(result[0],result[1],result[2],result[3]);
                    });
                });

                function calculateAndDisplayRoute(directionsService, directionsDisplay, pointA, pointB) {
                    var selectedMode = document.getElementById('mode').value;
                    directionsService.route({
                        origin: pointA,
                        destination: pointB,
                        avoidTolls: true,
                        avoidHighways: false,
                        travelMode: google.maps.TravelMode[selectedMode]
                    }, function (response, status) {
                        if (status == google.maps.DirectionsStatus.OK) {
                            scope.distance.value = response.routes[0].legs[0].distance.text;
                            scope.distance.duration = response.routes[0].legs[0].duration.text;
                            directionsDisplay.setDirections(response);
                        }
                    });
                }
            }
        }
    }

})();

