(function(){
    'use strict';

    angular
        .module('project')
        .controller('ElementCommentsController', ElementCommentsController);

    ElementCommentsController.$inject = ['userService', '$routeParams', 'firebaseService'];

    function ElementCommentsController(userService, $routeParams, firebaseService) {
        var elemComVm = this;

        elemComVm.actualPlace = $routeParams.element;
        elemComVm.userInfo = userService.user;
        elemComVm.showComments = showComments;
        elemComVm.isComment = false;

        elemComVm.readOnly = true;
        elemComVm.commentRate = 3;
        elemComVm.sendComment = sendComment;
        elemComVm.commentsArray = [];
        elemComVm.initForm = initForm;
        elemComVm.resetForm = resetForm;
        elemComVm.makeDate = makeDate;
        elemComVm.submitted=true;
        elemComVm.readyToUse = true;

        activate();

        function activate() {
           initForm();
        }

        function resetForm(form) {
            form.$setPristine();
            form.$setUntouched();
        }

        function initForm() {
            elemComVm.comment = {
                text: '',
                rate: 0,
                surname: '',
                name: ''
            };
        }

        function makeDate() {
            var d = new Date(),
                year = d.getFullYear(),
                month = d.getMonth() + 1,
                day = d.getDate(),
                fullDate = year+'-'+month+'-'+day;

            return fullDate;
        }

        function sendComment(comment) {
            comment['picture'] = userService.user.picture;
            comment['date'] = makeDate();
            elemComVm.submitted=false;

            firebaseService.addComment(elemComVm.actualPlace, comment);
            initForm();
            elemComVm.readyToUse = false;
        }

        function showComments() {
            if(!elemComVm.isComment){
                firebaseService.getPlaceComments(elemComVm.actualPlace)
                    .then(function(arr){
                        elemComVm.commentsArray = arr;
                    });
                elemComVm.isComment = true;
            }else{
                elemComVm.isComment = false;
            }
        }
    }

})();

(function() {
    'use strict';

    angular
        .module('project')
        .controller('TripPlannerController', TripPlannerController);

    TripPlannerController.$inject = ['FacebookService', 'userService', 'googleMapPositionService', 'cacheService', 'firebaseService'];

    function TripPlannerController(FacebookService, userService, googleMapPositionService, cacheService, firebaseService) {
        var tripVm = this;

        tripVm.excursion = [];
        tripVm.user = userService.user;
        tripVm.removeFromTrip = removeFromTrip;
        tripVm.googleMapApiReady = false;

        tripVm.onDropComplete = onDropComplete;
        tripVm.addToMap = addToMap;

        tripVm.limitToCheck = 9;
        tripVm.checkDisable = checkDisable;

        activate();

        function activate() {
            FacebookService.checkLoginState()
                .then(function(){
                    initGoogleMapApi();
                    getExcursionPlaces();
                });
        }

        function getExcursionPlaces() {
            if(cacheService.excursion.length === 0){
                firebaseService.getExcursionPlaces(userService.user)
                    .then(function(result){
                        tripVm.excursion = result;
                        cacheService.saveTrip(result);
                    });
            } else {
                tripVm.excursion = cacheService.getTrip();
            }
        }

        function initGoogleMapApi() {
            googleMapPositionService.onReady()
            .then(function(){
                tripVm.googleMapApiReady = true;
            });
        }

        function removeFromTrip(item) {
            firebaseService.removePlaceFromTrip(userService.user, item);
        }

        function onDropComplete(index, obj){
            var otherObj = tripVm.excursion[index],
                otherIndex = tripVm.excursion.indexOf(obj);
            tripVm.excursion[index] = obj;
            tripVm.excursion[otherIndex] = otherObj;
        }

        function addToMap(id) {
            tripVm.excursion.forEach(function(element) {
                if(element.id === id && element.trip){
                    if(element.showOnMap){
                        element.showOnMap = false;
                    } else {
                        element.showOnMap = true;
                    }
                    firebaseService.updateTripElement(clearObjBeforeUpdate(element));
                }
            });
        }

        function checkDisable(id){
            var onMap = [];
            tripVm.excursion.forEach(function(element) {
                if(element.showOnMap) { onMap.push(element.id); }
            });
            return tripVm.limitToCheck === onMap.length && !onMap.includes(id);
        }

        function clearObjBeforeUpdate(obj) {
            var cleaned = {
                fireId: obj.$id,
                category: obj.category,
                id: obj.id,
                location: obj.location,
                name: obj.name,
                picture: obj.picture,
                showOnMap: obj.showOnMap || false,
                trip: obj.trip
            };
            return cleaned;
        }

    }

})();

(function(){
    'use strict';

    angular
        .module('project')
        .filter('cut', cut);

    function cut() {
        return function (value, wordwise, max, tail) {
            if (!value) return '';

            max = parseInt(max, 10);
            if (!max) return value;
            if (value.length <= max) return value;

            value = value.substr(0, max);
            if (wordwise) {
                var lastspace = value.lastIndexOf(' ');
                if (lastspace != -1) {
                  if (value.charAt(lastspace-1) == '.' || value.charAt(lastspace-1) == ',') {
                    lastspace = lastspace - 1;
                  }
                  value = value.substr(0, lastspace);
                }
            }

            return value + (tail || ' …');
        };
    }

})();

(function() {
    'use strict';

    angular
        .module('project')
        .controller('ShowResultsController', ShowResultsController);

    ShowResultsController.$inject = ['$q', 'categories', '$location', '$window', 'errorHandling', 'cacheService', 'FacebookService', '$routeParams'];

    function ShowResultsController($q, categories, $location, $window, errorHandling, cacheService, FacebookService, $routeParams) {
        var resultVm = this;

        resultVm.listByCategory = {};
        resultVm.actualCategory = '';
        resultVm.loadMoreData = loadMoreData;
        resultVm.pagingNext = '';

        activate();

        resultVm.numberPerPage = 9;
        resultVm.maxSize = 5;
        resultVm.totalItems = null;
        resultVm.currentPage = 1;

        function activate() {
            checkActualCategory($routeParams.item)
                .then(function(route){
                    resultVm.actualCategory = route;
                    getCacheService();
                    resultVm.totalItems = resultVm.listByCategory[route].length;
                    checkFBState();
                })
                .catch(function(response){
                    errorHandling.errorFunc(response);
                });
        }

        function checkActualCategory(route){
            var deferred = $q.defer();
            if (categories.indexOf(route) > -1) {
                deferred.resolve(route);
            } else {
                //$location.path('category'); dlaczemu po tym przekierowaniu robi milion zapytan do FBapi :C idk
                deferred.reject('Actual category didnt exist in our service, please change category.');
            }
            return deferred.promise;
        }

        function checkFBState() {
            FacebookService.checkLoginState()
                .then(function(){
                    routeTypeRequest();
                });
        }

        function routeTypeRequest() {
            var capitalLetter = resultVm.actualCategory.charAt(0).toUpperCase() + resultVm.actualCategory.slice(1);
            var method = 'get'+capitalLetter+'s';

            resultVm.route = resultVm.actualCategory;
            if(resultVm.listByCategory[resultVm.actualCategory].length === 0){
                FacebookService[method]()
                    .then( function(response){
                        loadCheckSave(response);
                    })
                    .catch(function(err){
                        errorHandling.errorFunc(err);
                    });
            }
        }

        function loadMoreData() {
            if(typeof resultVm.pagingNext != 'undefined') {
                FacebookService.getMoreData(resultVm.pagingNext)
                    .then(function(response){
                        loadCheckSave(response);
                    })
                    .catch(function(err){
                        errorHandling.errorFunc(err);
                    });
            }
        }

        function loadCheckSave(response){
            resultVm.listByCategory[resultVm.actualCategory] = resultVm.listByCategory[resultVm.actualCategory].concat(response.data);
            cacheService.saveCache(resultVm.actualCategory, resultVm.listByCategory[resultVm.actualCategory]);
            resultVm.totalItems = resultVm.listByCategory[resultVm.actualCategory].length;
            if(response.paging){
                resultVm.pagingNext = response.paging.next;
            }else{ resultVm.pagingNext = undefined; }
        }

        function getCacheService() {
            resultVm.listByCategory = cacheService.getCache();

            angular.forEach(resultVm.listByCategory, function(awesomeThing) {
                awesomeThing.rank = Math.random();
            });
        }

    }
})();

